/**
 * canvas
 * @module ychart/core/graphic/contextview
 */

import View from "./view";
import Transform from "./mixin/transform";
import Eventful from "./mixin/eventful";
import Moveable from "./mixin/moveable";
import OptionProxy from "../config/OptionProxy";
import text from "./helper/text";

import {
    mixin
} from "../../tool/klass";

import {
    isPtInPath,
    isPtInRect
} from "./helper/viewutil";

import {
    getRectByCtx
} from "../../tool/dom.js";

/**
 * @classdesc canvasCanvasRenderingContext2D,
 *  
 * 
 * @class
 * @abstract
 */
class ContextView extends View {

    /**
     * @param {String} type  . ContextView
     * @param {Object} option  
     * @constructor
     */
    constructor(type = "ContextView", option = {}) {
        super(type, option);

        this.configProxy = new OptionProxy(this.defaultConfig, option);

        /**
         * 
         * ychart
         * @member {object}
         * @private
         */
        this.__yh = null;

        Transform.call(this, option);
        Eventful.call(this);
    }

    /**
     *
     * @property {boolean} Draggable 
     * @default true
     */
    get draggable() {
        return this.config.draggable === undefined ? true : this.config.draggable;
    }

    /**
     * canvas
     * @property {Number} zLevel
     * @default 0
     */
    get zLevel() {
        return this.config.zLevel || 0;
    }

    /**
     * @property {Object} config shapa
     * @returns {Object}
     */
    get config() {
        return this.configProxy.getConfig();
    }

    /**
     * @property {boolean} getable 
     * @default true
     * @return boolean
     */
    get getable() {
        return typeof this.config.getable === "undefined" ? true : this.config.getable;
    }

    /**
     * ychart. \
     */
    ReBrushAll() {
        this.__yh && this.__yh.update();
    }

    /**
     * context
     * @method
     * @param {CanvasRenderingContext2D} ctx
     */
    /* eslint-disable */
    BeforeBrush(ctx, config) {}

    /**
     * context
     * @method
     * @param {CanvasRenderingContext2D} ctx
     */
    AfterBrush(ctx, config) {}

    /**
     * 
     * @return {Array.<Number>} rect
     */
    GetContainRect() {
            return this.rect;
        }
        /*eslint-enable */

    /**
     * context
     * @method
     * @private
     * @param {CanvasRenderingContext2D} ctx
     */
    _BeforeBrush(ctx, config) {
        ctx.save();

        this._SetShapeTransform(ctx, config);

        this.configProxy.bindContext(ctx);

        this.BeforeBrush(ctx, config);

        ctx.beginPath();
    }

    /**
     * 
     * @method
     * @private
     * @param {CanvasRenderingContext2D} ctx
     */
    _SetShapeTransform(ctx) {
        this.updateTransform();
        this.setTransform(ctx);
    }

    /**
     * canvas
     * @param {CanvasRenderingContext2D} ctx
     */
    getRectByCtx(ctx) {
        return getRectByCtx(ctx);
    }

    /**
     * context
     * @method
     * @private
     * @param {CanvasRenderingContext2D} ctx
     */
    _AfterBrush(ctx, config) {
        var tp = this.configProxy.getBrushType();
        /* eslint-disable */
        switch (tp) {
            case "both":
            case "all":
                ctx.fill();
                ctx.stroke();
                break;
            case "stroke":
                ctx.stroke();
                break;
            case "fill":
                ctx.fill();
                break;
            case "none":
                break;
            default:
                ctx.fill();
                break;
        }
        /* eslint-enable */
        ctx.restore();

        this.AfterBrush(ctx, config);
    }

    /**
     *  
     * @abstract
     * @param {CanvasRenderingContext2D} ctx
     * @param {object} config --
     */
    /* eslint-disable*/
    BuildPath(ctx, config) {
            //
            throw new Error(" unsurported operation -- can't build shape path");
    }
    /* eslint-enable */

    /**
     *  
     * @method
     * @param {CanvasRenderingContext2D} ctx
     */
    Brush(ctx) {
        var config = this.config;
        if (!config.ignore) {
            //
            this._BeforeBrush(ctx, config);
            //
            this.BuildPath(ctx, config);
            //
            this._AfterBrush(ctx, config);

            this.DrawText(ctx, config);
        }
    }

    /**
     *  
     * @param {object} option
     */
    setOption(option) {
        this.configProxy.update(option);
        this.__dirty = true;
    }

    /**
     * .
     * @see {GetContainRect} , @see {_isPtInPath} 
     * @param {Number} x   x
     * @param {Number} y   y
     */
    contain(x, y) {
        var local = this.transformCoordToLocal(x, y);
        //containRect, 
        return this.getable &&
            (isPtInRect(this.GetContainRect(), local[0], local[1]) &&
                this._isPtInPath(x, y));
    }

    /**
     * 
     * @param x
     * @param y
     * @private
     */
    _isPtInPath(x,y){
        return isPtInPath(this, this.config, x, y);
    }

    /**
     * 
     * @param {CanvasRenderingContext2D} ctx
     * @param {object} config --
     */
    DrawText(ctx, config) {
        if (!config.text) {
            return;
        }
        var crect = this.GetContainRect();
        if (!crect) {
            return;
        }
        var x = crect[0] + (crect[2] - crect[0]) / 2;
        var y = crect[1] + (crect[3] - crect[1]) / 2;

        var st = this.configProxy.getStyle();
        /* eslint-disable */
        var textw = text.getTextWidth(config.text, st.font);
        var texth = text.getTextHeight(config.text ,st.font);
        var textAlign = st.textAlign , textBaseline = st.textBaseline;
        switch (config.textpos) {
            case "bottom-center":
                y = crect[1];
                textAlign = "center";
                break;
            case "top-left":
            case "left-top":
                x = crect[0];
                y = crect[1];
                break;
            case "bottom-right":
            case "right-bottom":
                x = crect[2] - textw;
                y = crect[1];
                break;
            case "left-center":
                x = crect[0];
                textAlign = "left";
                textBaseline = "middle";
                break;
            case "left-bottom":
            case "bottom-left":
                x = crect[0];
                y = crect[3];
                break;
            case "top-center":
                y = crect[3];
                textAlign = "center";
                textBaseline = "top";
                // x -= textw/2;
                break;
            case "top-right":
            case "right-top":
                y = crect[3];
                x = crect[2]-textw;
                break;
            case "right-center":
                x = crect[2]-textw;
                y -= texth/2;
                break;
            default:
                textAlign = "center";
                textBaseline = "middle";
        }
        /* eslint-disable */
        ctx.save();
        //
        if (st.textColor) {
            ctx.fillStyle = st.textColor;
        }
        //
        var globalTextPos = this.transformCoordToGlobal(x,y);
        x = globalTextPos[0] ,y=globalTextPos[1];

        text.fillText(ctx, config.text, x, y, st.font, textAlign, textBaseline);

        ctx.restore();
    }
}


mixin(ContextView, Transform, true);
mixin(ContextView, Eventful, true);
mixin(ContextView, Moveable, true);

export default ContextView;



