/**
 * @module ychart/core/graphic/mixin
 */


/**
 * Dispatch
 *  ReactorDispatch
 * @class
 * @mixin
 */
var Eventful = function () {
    this._handlers = {};
};

/**
 * 
 * @param event  
 * @param handler 
 * @param context 
 * @returns {Eventful}
 */
Eventful.prototype.on = function (event, handler, context) {
    var h = this._handlers;
    if (!h || !event || !handler) {
        return this;
    }

    if (!h[event]) {
        h[event] = [];
    }
    h[event].push({
        _h: handler,
        _one: false,
        _ctx: context || this  //
    });

    return this;
};

/**
 * . trigger
 * @param event  
 * @param handler 
 * @param context 
 * @returns {Eventful}
 */
Eventful.prototype.once = function (event, handler, context) {
    var _h = this._handlers;

    if (!handler || !event) {
        return this;
    }

    if (!_h[event]) {
        _h[event] = [];
    }

    if (_h[event].indexOf(handler) >= 0) {
        return this;
    }

    _h[event].push({
        _h: handler,
        _one: true,
        _ctx: context || this  //
    });

    return this;
};

/**
 * /
 * @param event  
 * @param handler   
 * @returns {Eventful}
 */
Eventful.prototype.remove = function (event, handler) {
    if (!event) {
        this._handlers = {};
        return this;
    }

    var hs = this._handlers[event];
    if (handler) {
        var i = 0;
        for (; i < hs.length; i++) {
            if (hs[i]._h === handler) {
                break;
            }
            hs.splice(i, 1);
        }
        if (hs && hs.length == 0) {
            delete this._handlers[event];
        }
    } else {
        delete this._handlers[event];
    }

    return this;
};

var arrySlice = Array.prototype.slice;

/**
 * 
 * @param eventName 
 * @returns {Eventful}
 */
Eventful.prototype.trigger = function (eventName) {
    var handles = this._handlers[eventName];
    if (handles) {
        var args = arguments;
        var argLen = args.length;

        if (argLen > 3) {
            args = arrySlice.call(args, 1);
        }
        //backbone,,
        handles.forEach(function (item, index, array) {
            switch (argLen) {
                case 1:
                    item._h.call(item._ctx);
                    break;
                case 2:
                    item._h.call(item._ctx, args[1]);
                    break;
                case 3:
                    item._h.call(item._ctx, args[1], args[2]);
                    break;
                default:
                    item._h.apply(item._ctx, args);
                    break
            }
            if (item._one) {
                array.splice(index, 1);
            }
        });
    }
    return this;
};


export default Eventful;



