/**
 * ychart
 * @module ychart/core/graphic/element
 */
import guid from "../../tool/guid";

/**
 * @classdesc  ID
 * @class
 */
class Element {
    /**
     * @constructor
     * @param {String} type  . element
     */
    constructor(type = "element") {
        Element.type = type;
        this.id = this.type + "_" + guid();
    }

    /**
     * id
     * @returns {string}  ID
     */
    getId() {
        return this.id;
    }

}

export default Element;



