/**
 * 
 * @module ychart/core/config/style
 */
var styleMap = {

    fillStyle: "fillStyle",
    fillColor: "fillStyle",
    color: "fillStyle",

    strokeStyle: "strokeStyle",
    lineColor: "strokeStyle",
    lineWidth: "lineWidth",
    lineCap: "lineCap",
    lineJoin: "lineJoin",
    font: "font",

    textAlign: "textAlign",
    textBaseline: "textBaseline",

    shadowColor: "shadowColor",
    shadowOffsetX: "shadowOffsetX",
    shadowOffsetY: "shadowOffsetY",
    shadowBlur: "shadowBlur",
    shadowx: "shadowOffsetX",
    shadowy: "shadowOffsetY",

    globalAlpha: "globalAlpha",
    alpha: "globalAlpha",
    globalCompositionOperation: "globalCompositionOperation",
    overlaystyle: "globalCompositionOperation"
};


/**
 * @classdesc  
 * 
 * canvas.
 * 
 * CONFIGstyle
 * @class
 */
var style = function () {
};


style.prototype = {
    /**
     * 
     * 
     *  : lineColor
     * @type string
     * @default blue
     */
    strokeStyle: "blue",

    /**
     * fill
     * 
     *   fillColor  color
     * @type string
     * @default #dcd5d9
     */
    fillStyle: "#dcd5d9",

    /**
     * 
     * @type number
     * @default 1
     */
    lineWidth: 1,

    /**
     * . buttroundsquare
     * @type string
     * @default round
     */
    lineCap: "round",

    /**
     * bevel,miter. ,.
     * @type string
     * @default round
     */
    lineJoin: "round",

    /**
     * 
     * @type string
     * @default bold 14px Arial, Helvetica, sans-serif, Times, serif
     */
    font: "bold 14px Arial, Helvetica, sans-serif, Times, serif",

    /**
     *  strokeStyle
     * canvasycharts
     * @type string
     * @default black
     */
    textColor: "black", // canvas

    /**
     * 
     * @type string
     * @default start
     */
    textAlign: "start",

    /**
     * 
     * @type string
     * @default bottom
     */
    textBaseline: "bottom",

    /**
     * 
     * @type string
     * @default #EA9090
     */
    shadowColor: "#EA9090",

    /**
     * X
     * @type number
     * @default  shadowOffsetX
     */
    shadowOffsetX: 0,

    /**
     * Y
     * @type number
     * @default shadowOffsetY
     */
    shadowOffsetY: 0,

    /**
     * 
     * @type number
     * @default 0
     */
    shadowBlur: 0,

    /**
     *   0
     * @type number
     * @default 1
     */
    globalAlpha: 1,

    /**
     * 
     * @type string
     * @default source-over
     */
    globalCompositionOperation: "source-over"

};


export default {
    style: style,
    styleMap: styleMap
};




