/**
 * View
 * @module ychart/core/graphic/view
 */

import Element from "./element";
/**
 * @class
 * @classdesc  
 * @abstract
 */
class View extends Element {

    /**
     * 
     * @constructor
     * @param {String} type view
     * @param {Object} option
     */
    constructor(type = "view", option = {}) {
        super(type);

        /**
         * 
         * @member {boolean}  true
         * @private
         */
        this.__dirty = true;

        /**
         * 
         * @member {object} null
         */
        this.parent = null;

        /**
         *  
         * @member {boolean}
         */
        this.ignore = option.ignore || false;
    }

    /**
     *  
     * @abstract
     * @param {Object} option 
     * @throws Error  
     */
    /* eslint-disable */
    Brush(option) {
        throw new Error("");
    }
    /* eslint-enable */
}

export default View;



