/**
 * canvas layerzIndex
 * @module ychart/layer
 */
import {
    checkNull
} from "./tool/util";
import {
    createDOM,
    getContext
} from "./tool/dom";
import Moveable from "./core/graphic/mixin/moveable.js";
import Transform from "./core/graphic/mixin/transform.js";
import {
    mixin
} from "./tool/klass.js";

/**
 *
 * @class
 * @classdesc  DOMcanvas
 *   layer layerStorage layerPainter
 */
class Layer {

    /**
    * @param id {string} LayerID
    * @param zLevel {Number}  zLevel
    * @param opts {object}
    * @constructor
    */
    constructor(id, zLevel, opts) {
        //ID
        this.id = id;
        //DOM. canvas
        this.dom = document.getElementById(id);
        //.
        this.zLevel = zLevel;

        if (checkNull(this.dom)) {
            this.dom = createDOM(id, "canvas", "layer", "absolute", {left: opts.left || 0,top: opts.top || 0} ,
                {width: opts.width ,height: opts.height});
        }

        this.dom.setAttribute("zLevel", zLevel);

        this.ctx = getContext(this.dom);

        this.layerW = opts.width;
        this.layerH = opts.height;

        if (checkNull(this.ctx)) {
            alert("HTML5 canvas,ychart .  " );
            return;
        }

        //
        this.__dirty = false;

        Transform.call(this);
    }

    /**
     * zlevel
     * @return {Number} zLevel  
     */
    getZlevel() {
        return this.zLevel;
    }

    /**
     * 
     * @returns {CanvasRenderingContext2D}
     */
    getContext() {
        return this.ctx;
    }

    /**
     * layer
     */
    clear() {
        this.ctx.clearRect(0, 0, this.layerW, this.layerH);
    }

    /**
     * layer canvas
     *   canvas
     * @param {Number} width  
     * @param {Number} height  
     */
    resize(width, height) {
        var dx = width / this.layerW,
            dy = height / this.layerH;

        this.zoom(dx, dy);

        //layer
        //Group
        this.updateTransform();

        this.dom.width = width;
        this.dom.height = height;
        this.layerW = width, this.layerH = height;
    }
}

mixin(Layer, Transform, true);
mixin(Layer, Moveable, true);

export default Layer;



