/**
 * @module ychart/core/graphic/mixin
 */


/**
 *  
 * @class
 * @mixin
 */
var Moveable = function () {

};

Moveable.prototype = {

    /**
     * 
     * @param {number} dx
     * @param {number} dy
     */
    drift: function (dx, dy) {
        this.position[0] += dx;
        this.position[1] -= dy;
        this.__dirty = true;
    },

    /**
     *  drift
     * @param {number} dx
     * @param {number} dy
     */
    move: function (dx, dy) {
        this.drift(dx, dy);
    },

    /**
     * 
     * @param {number}  x X
     * @param {number}  y y
     * @param {number}  angle 
     */
    rotate: function (x, y, angle) {
        if(!this.rotation){
            this.rotation = 0;
        }
        this.origin[0] += x;
        this.origin[1] += y;
        this.rotation += angle;

        this.__dirty = true;
    },

    /**
     * 
     * . [0,0] ,
     * shapeorigin
     *
     * @param {number}  x X
     * @param {number}  y y
     */
    zoom: function (x, y) {
        this.scale[0] *= x;
        this.scale[1] *= y;
        this.__dirty = true;
    }
};


export default Moveable;



