//

import Style from "../../config/style";
import {
    getContext
} from "./viewutil";

var st = new Style.style();

var defaultFont = st.font;
var defaultAlign = st.textAlign;
var defaultBaseline = st.textBaseline;

var _ctx = getContext();

var TextUtil = {
    TEXT_CACHE_MAX: 5000,

    _textHeightCache: [],
    _textHeightCacheCounter: 0,

    getTextHeight: function(text, textFont) {
        textFont = textFont || defaultFont;
        var key = text + ":" + textFont;
        if (TextUtil._textHeightCache[key]) {
            return TextUtil._textHeightCache[key];
        }

        _ctx.save();
        if (textFont) {
            _ctx.font = textFont;
        }

        text = (text + "").split("\n");
        // 
        var height = (_ctx.measureText("").width + 2) * text.length;

        _ctx.restore();

        TextUtil._textHeightCache[key] = height;
        if (++TextUtil._textHeightCacheCounter > TextUtil.TEXT_CACHE_MAX) {
            // 
            TextUtil._textHeightCacheCounter = 0;
            TextUtil._textHeightCache = {};
        }
        return height;
    },
    _textWidthCache: [],
    _textWidthCacheCounter: 0,

    getTextWidth: function(text, textFont) {

        textFont = textFont || defaultFont;
        var key = text + ":" + textFont;
        if (TextUtil._textWidthCache[key]) {
            return TextUtil._textWidthCache[key];
        }
        _ctx.save();

        if (textFont) {
            _ctx.font = textFont;
        }

        text = (text + "").split("\n");
        var width = 0;
        for (var i = 0, l = text.length; i < l; i++) {
            width = Math.max(
                _ctx.measureText(text[i]).width,
                width
            );
        }
        _ctx.restore();

        TextUtil._textWidthCache[key] = width;
        if (++TextUtil._textWidthCacheCounter > TextUtil.TEXT_CACHE_MAX) {
            // 
            TextUtil._textWidthCacheCounter = 0;
            TextUtil._textWidthCache = {};
        }

        return width;
    },
    /*
     * @param {Canvas} ctx 
     * @param {string} text 
     * @param {number} x X
     * @param {number} y Y
     * @param {string} textFont 
     */
    getTextRect: function(text, x, y, textFont, textAlign, textBaseline) {
        textFont = textFont || defaultFont;
        textAlign = textAlign || defaultAlign;
        textBaseline = textBaseline || defaultBaseline;

        var width = TextUtil.getTextWidth(text, textFont);
        var lineHeight = TextUtil.getTextHeight("", textFont);

        text = (text + "").split("\n");

        switch (textAlign) {
        case "end":
        case "right":
            x -= width;
            break;
        case "center":
            x -= (width / 2);
            break;
        }

        switch (textBaseline) {
        case "top":
            break;
        case "bottom":
            y -= lineHeight * text.length;
            break;
        default:
            y -= lineHeight * text.length / 2;
        }

        return {
            x: x,
            y: y,
            width: width,
            height: lineHeight * text.length
        };
    }
};


function _fillText(ctx, text, x, y, textFont, textAlign, textBaseline) {
    if (textFont) {
        ctx.font = textFont;
    }
    ctx.textAlign = textAlign;
    ctx.textBaseline = textBaseline;

    var rect = TextUtil.getTextRect( text, x, y, textFont, textAlign, textBaseline );

    text = (text + "").split("\n");

    var lineHeight = TextUtil.getTextHeight("", textFont);
    var _left = rect.x , _right = _left + rect.width;
    var _top = y + lineHeight*text.length , _bottom = y;
    switch (textBaseline) {
    case "top":
        y = rect.y;
        _top = y;
        _bottom = y + lineHeight*text.length;
        break;
    case "bottom":
        y = rect.y + lineHeight;
        break;
    default:
        y = rect.y + lineHeight / 2;
    }
    for (var i = 0, l = text.length; i < l; i++) {
        ctx.fillText(text[i], x, y);
        y += lineHeight;
    }

    return {
        left: _left,
        bottom:_bottom,
        top: _top,
        right: _right
    };
}

export default {
    fillText: _fillText,
    getTextRect: TextUtil.getTextRect,
    getTextWidth: TextUtil.getTextWidth,
    getTextHeight: TextUtil.getTextHeight
};



